.Boost {
  padding-top: 100px;
  padding-bottom: 100px;
  text-align: center;
  background-color: var(--secondary-color);
  background-position: top left;
  background-repeat: no-repeat;
  background-size: cover;
}
.Boost-title {
  color: var(--white-color);
  margin-bottom: 20px;
}

@media screen and (min-width: 720px) {
  .Boost {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
